<template><div class="content"><h1 class="post-title">短期锐评<a class="header-anchor" id="%E7%9F%AD%E6%9C%9F%E9%94%90%E8%AF%84" href="#%E7%9F%AD%E6%9C%9F%E9%94%90%E8%AF%84">#</a></h1><div class="metabar"><div class="metabar-item">2022/03/18</div><div class="metabar-item">思想</div><div class="metabar-item">约 5 分钟读完</div></div>
<p>我从来没有想过，是否存在着这样一种虚伪，你活在之中，是那么的自然。所有人都向着你的时候，他们让你以为这是理所当然；而当你无法给予或者违背之时，那么就是忘恩负义、不知廉耻了。</p>
<p>我从来没有想过，是否存在着这样一种偏见，你永远接受，是那么的自然。所有人都支持他们的时候，你以为这是人伦天性；而当你发现这一切竟是剥夺与残害、歧视与挖苦的时候，你却又发现自己无能为力了。</p>
<p>我从来没有想过，是否存在着这样一种「被受益」，你屹立在其中，不知不觉而岿然不动。纵使你相信命运永不可被旁人所标定，他人却将你被捧在手心的事实演化得淋漓尽致。</p>
<p>我想，若是顺从，也便成了帮凶，更何况这不能给我带来什么好处，所以我坚决不会做；若是反叛，也便成了混蛋，更何况他们对你有了那么多的施予，所以我不得不考虑。</p>
<p>若把这份痛苦的纠结理解为恶毒，未免过于曲解人类的某些动物性本能，即使这份本能早在人类开化以后与对利益的追求和欲望完全混淆；若把这份痛苦的纠结理解为本分，当然是不大可能，因为这份思想不是什么好思想，这样的传统不是什么好传统，信奉它的人自然也不是什么好人了——从最根本地来说，我们为什么要损害别人的利益呢？人皆有不忍人之心。不过，这句话的提出者也许两面三刀，但我不是。</p>
<p>想多了吗？我觉得没有。这是可以发生在每一个人身上的事情，让我感到别扭，是因为我厌恶这样一种眼前的物质条件虽已被我自己当成常态，但最终却发现这是他们牺牲了有些人的欢愉与兴致乃至人生看法专门换来的多余品。对此，我无法改变，无法弥补，无法适应缺失以后的身份活，更无法接受他人被伤害。</p>
<p>至于一些只会谈世俗和眼前利益的肤浅之人，我的观点只能概括为多说无益，无可奉告。</p>
</div></template>